:root {
    --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    --animation-duration: 250ms;
}

li {
    list-style: none;
}

.search-form {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
}

.search-form-btn {
    margin-left: 10px;
}

.load-more {
    display: flex;
    margin: 0 auto;
}

.gallery {
    /* position: relative; */
     display: grid; 
    max-width: 1140px;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-auto-rows: 240px;
    grid-gap: 12px;
    justify-content: center;
    margin-left: auto;
    margin-right: auto; 
}

